// Service provider component
function ProviderCard({ provider, onSelect }: { provider: any; onSelect: (provider: any) => void }) {
  return (
    <div
      className="border border-gray-200 rounded-lg shadow-sm bg-white overflow-hidden cursor-pointer hover:shadow-md transition-shadow"
      onClick={() => onSelect(provider)}
    >
      <div className="p-5">
        <div className="flex items-center space-x-4 space-x-reverse">
          <div className="h-12 w-12 rounded-full overflow-hidden bg-gray-200">
            <Image
              src={provider.imageUrl}
              alt={provider.name}
              width={48}
              height={48}
              className="h-full w-full object-cover"
            />
          </div>
          <div>
            <h3 className="text-lg font-semibold text-gray-900">{provider.name}</h3>
            <div className="flex items-center text-sm text-gray-500">
              <span className="flex items-center">
                <svg
                  className="w-4 h-4 ml-1 text-yellow-400"
                  fill="currentColor"
                  viewBox="0 0 20 20"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z"></path>
                </svg>
                {provider.rating}
              </span>
              <span className="mr-2 border-r border-gray-300 pr-2">
                {provider.completedJobs} مشروع منجز
              </span>
            </div>
          </div>
        </div>
        <p className="mt-3 text-sm text-gray-600 line-clamp-3">{provider.bio}</p>
      </div>
    </div>
  );
}

// Request form component
function RequestForm({ provider, onClose }: { provider: any; onClose: () => void }) {
  const [description, setDescription] = useState('');
  const [showSuccess, setShowSuccess] = useState(false);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    // Handle request logic here
    console.log('Request submitted for provider:', provider.id, 'Description:', description);
    setShowSuccess(true);
  };

  if (showSuccess) {
    return (
      <div className="bg-white p-6 rounded-lg shadow-md">
        <div className="text-center">
          <svg
            className="mx-auto h-12 w-12 text-green-600"
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M5 13l4 4L19 7"
            />
          </svg>
          <h3 className="mt-2 text-xl font-semibold text-gray-900">تم إرسال طلبك بنجاح</h3>
          <p className="mt-2 text-gray-600">
            تم إرسال طلبك إلى {provider.name}. سيقوم بمراجعة الطلب والرد عليك في أقرب وقت.
          </p>
          <button
            onClick={onClose}
            className="mt-4 inline-flex justify-center rounded-md border border-transparent bg-indigo-600 px-4 py-2 text-sm font-medium text-white shadow-sm hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2"
          >
            العودة
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="bg-white p-6 rounded-lg shadow-md">
      <div className="flex justify-between items-center mb-4">
        <h3 className="text-lg font-semibold text-gray-900">طلب خدمة من {provider.name}</h3>
        <button
          onClick={onClose}
          type="button"
          className="text-gray-500 hover:text-gray-700"
        >
          <span className="sr-only">إغلاق</span>
          <svg
            className="h-6 w-6"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M6 18L18 6M6 6l12 12"
            />
          </svg>
        </button>
      </div>
      <form onSubmit={handleSubmit}>
        <div className="mb-4">
          <label
            htmlFor="description"
            className="block text-sm font-medium text-gray-700 mb-2"
          >
            وصف الخدمة المطلوبة
          </label>
          <textarea
            id="description"
            rows={5}
            className="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
            placeholder="اشرح ما تحتاجه بالتفصيل..."
            value={description}
            onChange={(e) => setDescription(e.target.value)}
            required
          />
        </div>
        <div className="mt-6 flex justify-end">
          <button
            type="submit"
            className="inline-flex justify-center rounded-md border border-transparent bg-indigo-600 px-4 py-2 text-sm font-medium text-white shadow-sm hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2"
          >
            إرسال الطلب
          </button>
        </div>
      </form>
    </div>
  );
}

export default function ServiceRequestPage() {
  const searchParams = useSearchParams();
  const [category, setCategory] = useState<string | null>(null);
  const [filteredProviders, setFilteredProviders] = useState(providers);
  const [selectedProvider, setSelectedProvider] = useState<any | null>(null);

  useEffect(() => {
    const categoryParam = searchParams.get('category');
    setCategory(categoryParam);

    if (categoryParam) {
      const filtered = providers.filter((provider) =>
        provider.categories.includes(categoryParam)
      );
      setFilteredProviders(filtered);
    } else {
      setFilteredProviders(providers);
    }
  }, [searchParams]);

  return (
    <>
      <Navbar />
      <main className="bg-gray-50 py-10">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center mb-10">
            <h1 className="text-3xl font-bold tracking-tight text-gray-900 sm:text-4xl">
              طلب خدمة الذكاء الاصطناعي
            </h1>
            {category && (
              <p className="mt-4 text-lg text-gray-600">
                تصفح مقدمي الخدمة المتخصصين في {category.replace('-', ' ')}
              </p>
            )}
          </div>

          {selectedProvider ? (
            <div className="max-w-2xl mx-auto">
              <RequestForm
                provider={selectedProvider}
                onClose={() => setSelectedProvider(null)}
              />
            </div>
          ) : (
            <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
              {filteredProviders.map((provider) => (
                <ProviderCard
                  key={provider.id}
                  provider={provider}
                  onSelect={setSelectedProvider}
                />
              ))}
            </div>
          )}
        </div>
      </main>
      <Footer />
    </>
  );
} 